<template>
    <div>
        <p>Hallo {{ name }}</p>
        <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit. Nunc sit amet urna leo. Duis nunc libero, gravida ac orci et, viverra venenatis mi. Nunc quis placerat dolor. Quisque sit amet eleifend metus, in mattis lectus. Suspendisse scelerisque est non magna ultrices hendrerit. Phasellus quis odio porta, mollis metus at, tincidunt sem. Suspendisse porttitor risus ex, et placerat velit commodo id. Duis dapibus feugiat placerat. Donec non massa risus. Duis in fringilla libero, et pharetra sem. Nullam euismod turpis tortor, quis pharetra eros semper faucibus.

Nunc volutpat massa risus, lacinia tristique purus tempus sit amet. Morbi sed egestas diam. Fusce tempor vitae nisl at semper. In mattis metus nisl, non vulputate neque convallis at. Pellentesque condimentum auctor scelerisque. Sed feugiat risus vitae purus ornare, nec venenatis nunc pharetra. Donec et tortor nec felis cursus commodo vitae nec velit. Integer quis mauris eu libero lobortis condimentum. Pellentesque vel erat eu elit commodo porttitor. Duis vel risus velit. Vivamus non urna ligula. Aenean condimentum diam orci, in feugiat augue facilisis a.</p>
        <div>
            <input type="text" v-model="name_input" placeholder="Voornaam"/>
            <button @click="changeName">Verander mijn naam!</button>
        </div>
    </div>
</template>

<script>
    console.clear( );
    
    export default {
        beforeCreate( ){
            console.log( "beforeCreate" );
        },
        created( ){
            console.log( "created" );
            
            this.fetcher = fetch( "https://raw.githubusercontent.com/AndreSteenveld/Chessy/master/package.json", { mode: "cors" } )
                .then(( response ) => response.json( ));
        },
        beforeMount( ){
            console.log( "beforeMount" );
        },
        mounted( ){
            console.log( "mounted" );
            this.getName( );
        },
        beforeUpdate( ){
            console.log( "beforeUpdate" );
        },
        updated( ){
            console.log( "updated" );
        },
        activated( ){
            console.log( "activated" );
        },
        data( ){
            console.log( "data" );
            
            return { 
                
                name: null,
                name_input: null
            
            };
        },
        
        methods: {
            getName( ){
                console.log( "getName" );
                
                this.fetcher
                    .then(( json ) => this.name = json.name );
            },
            changeName( ){
                this.name = this.name_input || this.name;
            }
        }
    }
</script>



